import { get, post, config } from '@/utils/api.js';
export default {
	name: 'getCash',
	props: {
		// 页面来源
		getCashSource: {
			type: String,
			default: '1'
		},
		currentMoney: {
			type: String,
			default: '0'
		},
		// 是否展示
		getCashIsShow: {
			type: Boolean,
			default: false
		},
		// 是否 开启动画
		isStartAnimation: {
			type: Boolean,
			default: false
		},
	},
	data() {
		return {
			staticUrl: config.staticUrl,
			getCashAni: '', // 动画所需配置
			cardAni: '', // 动画所需配置
			hongbaoMesAni: '' // 动画所需配置
		};
	},
	methods: {
		// 关闭弹窗
		closegetCash() {
			const that = this;
			that.$emit('getCashColse', false);
		},
		// 开始动画
		startAnimation() {
			console.log(3);
			const that = this;
			// 整个盒子动效 从小到大
			const getCashAnimation = wx.createAnimation({
				duration: 500,
				timingFunction: 'ease',
				delay: 500
			});
			getCashAnimation.scale(1).step();
			that.getCashAni = getCashAnimation.export();

			// 盒子内 卡片动向 从下向上滑出
			const cardAnimation = wx.createAnimation({
				duration: 500,
				timingFunction: 'ease',
				delay: 1000
			});
			cardAnimation
				.opacity(1)
				.translateY(0)
				.step();
			that.cardAni = cardAnimation.export();
			
			// 盒子内 卡片动向 从下向上滑出hongbaoMesAni
			const hongbaoMesAnimation = wx.createAnimation({
				duration: 500,
				timingFunction: 'ease',
				delay: 1000
			});
			hongbaoMesAnimation
				.opacity(1)
				.translateY(0)
				.step();
			that.hongbaoMesAni = hongbaoMesAnimation.export();
			
		},
		// 父组件触发
		isStartAnimationFun(e) {
			const that = this;
			this.startAnimation();
			if (this.isStartAnimation) {
				this.startAnimation();
			}
		}
	}
};
